<template>
	<div class="col-xs-offset-2 col-xs-8">
		<div class="page-header">
			<h2>Vue Router Demo</h2>
			<button @click="back">后退</button>
			<button @click="forward">前进</button>
			<button @click="go">go</button>
		</div>
	</div>
</template>

<script>
export default {
	name: 'Banner',

	data() {
		return {}
	},

	mounted() {},

	methods: {
		back(){
			this.$router.back()
		},
		forward(){
			this.$router.forward()
		},
		//传递数值，-代表后退，+代表前进
		go(){
			this.$router.go(1)
		}
	},
}
</script>

<style lang="scss" scoped></style>
