//该文件专门用于创建整个应用的路由器
import VueRouter from 'vue-router'
import About from '../pages/About.vue'
import Home from '../pages/Home.vue'
import News from '../pages/News.vue'
import Message from '../pages/Message.vue'
import Detail from '../pages/Detail.vue'
//创建并暴露一个路由器
const router = new VueRouter({
	//工作模式，默认为hash模式（#）
	mode:'history',
	routes: [
		{
			name: 'guanyu',
			path: '/about',
			component: About,
			meta: {
				isAuth: true,

				title: '关于',
			},
		},
		{
			name: 'zhuye',
			path: '/home',
			component: Home,
			meta: {
				title: '主页',
			},
			children: [
				//通过children配置子级路由
				{
					name: 'xinwen',
					path: 'news', //此处一定不要写：/news
					component: News,
					meta: {
						isAuth: true,
						title: '新闻页',
					},
					//独享路由守卫
					// beforeEnter(to, from, next) {
					// 	console.log('独享', to, from)
					// 	if (to.meta.isAuth) {
					// 		//判断当前路由是否需要进行权限控制
					// 		if (localStorage.getItem('school') === 'atguigu') {
					// 			next()
					// 		} else {
					// 			alert('暂无权限查看')
					// 			// next({name:'guanyu'})
					// 		}
					// 	} else {
					// 		next()
					// 	}
					// },
				},
				{
					name: 'xiaoxi',
					path: 'message', //此处一定不要写：/message
					component: Message,
					meta: {
						isAuth: true,
						title: '消息页',
					},

					children: [
						{
							name: 'xiangqing',
							path: 'detail/:id/:title', //使用占位符声明接收params参数
							component: Detail,
							meta: {
								title: '详情',
							},
							//1.对象写法,key/value以props的形式传给组件
							// props: {
							// 	a: 1,
							// 	b: 'hello',
							// },

							//2.布尔值写法,把接受到的params参数以props传递给组件
							// props: true

							//3.函数写法，可以接收两种参数
							props($route) {
								return {
									id: $route.params.id,
									title: $route.params.title,
								}
							},
						},
					],
				},
			],
		},
	],
})
//全局前置路由守卫：初始化执行，每次路由切换之前被调用，
// router.beforeEach((to, from, next) => {
// 	console.log('beforeEach', to, from)
// 	// if (to.name==='xinwen'||to.name==='xiaoxi') {
// 	//判断当前路由是否需要进行权限控制
// 	if (to.meta.isAuth) {
// 		if (localStorage.getItem('school') === 'atguigu') {
// 			//权限控制的具体规则
// 			next() //放行
// 		} else {
// 			alert('暂无权限查看')
// 			// next({name:'guanyu'})
// 		}
// 	} else {
// 		next() //放行
// 	}
// })

//全局后置守卫：初始化时执行、每次路由切换后执行,没有next
router.afterEach((to, from) => {
	console.log('afterEach', to, from)
	if (to.meta.title) {
		document.title = to.meta.title //修改网页的title
	} else {
		document.title = 'vue_test'
	}
})
export default router
